* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Kanit", sans-serif;
}

/* Navbar */
.Navbar {
  background-color: white;
  box-shadow: 1px 1px 10px grey;
  height: 80px;
  width: 100%;
  top: 0;
  position: fixed;
  z-index: 1;
}
ul {
  list-style: none;
  margin-left: 100px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
li {
  padding: 2em 1.3rem;
}
li img {
  width: 50px;
  height: 50px;
  border-radius: 100%;
  display: block;
  margin-top: -18px;
}
li a {
  color: black;
  text-decoration: none;
  font-size: 40px;
  font-weight: bold;
  display: block;
  margin-top: -18px;
}
.left-nav {
  display: flex;
}
.right-nav {
  display: flex;
  margin-right: 100px;
  position: relative;
}
.list {
  flex-direction: row;
  display: flex;
}
.search {
  margin-top: 15px;
  width: 850px;
  height: 50px;
  border-radius: 20px;
  background-color: silver;
  align-items: center;
  display: flex;
  color: gray;
}
.number {
  position: absolute;
  right: 108px;
  top: 20px;
  border-radius: 100%;
  background-color: skyblue;
  height: 20px;
  width: 18px;
  text-align: center;
}
.mycart {
  position: fixed;
  width: 300px;
  height: 370px;
  right: 100px;
  background-color: white;
  box-shadow: 1px 1px 10px grey;
  margin-top: -40px;
}
.mycart img {
  width: 20%;
}
.mycart h4 {
  text-align: center;
  font-weight: bold;
  margin-top: 10px;
}
.mycart h5 {
  text-align: center;
  font-size: 7px;
}
.mycart a {
  text-decoration: none;
}
.mycart p {
  text-align: center;
}
.viewcart {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80%;
  height: 40px;
  margin-left: 30px;
  background-color: white;
  border: 2px solid skyblue;
  border-radius: 20px;
}
.totalbook {
  display: flex;
}
.totalbook h3 {
  margin-left: 40px;
  font-size: 17px;
  font-weight: bold;
}
.totalbook img,
.totalbook p {
  margin-left: 10px;
}
.gopurchase {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 30px;
  width: 80%;
  height: 40px;
  background-color: rgb(31, 146, 192);
  color: white;
  border-radius: 20px;
}
/* Navbar */

/* slide */

.slide {
  top: 80px;
  margin-left: 130px;
  margin-right: 130px;
  width: 100% auto;
  height: 400px;
  background-color: silver;
  position: relative;
}
.btn-left {
  position: absolute;
  top: 170px;
  width: 30px;
  height: 30px;
  background-color: gray;
  border-radius: 60%;
  text-align: center;
}
.btn-right {
  position: absolute;
  right: 0;
  width: 30px;
  height: 30px;
  text-align: center;
  background-color: gray;
  top: 170px;
  border-radius: 60%;
}
/* slide */

/* bestSell and reccommend */
.bestSell {
  top: 150px;
  margin-left: 130px;
  margin-right: 130px;
  position: relative;
}
.tittle {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.tittle h4 {
  font-weight: bold;
}
.tittle p {
  font-weight: bold;
  text-decoration: underline;
}
.book {
  width: 230px;
  height: 350px;
}
.none {
  text-decoration: line-through;
}
.price {
  font-weight: bold;
}
.star-review {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
.star {
  display: flex;
}
.review {
  display: flex;
}
.shelf {
  padding: 15px 30px;
  display: flex;
  justify-content: space-between;
}
.shelf p {
  text-align: center;
}
.shelf a {
  color: black;
  text-decoration: none;
}
.book img {
  width: 120px;
  height: 150px;
  margin-left: 50px;
  margin-top: 10px;
}
.book:hover {
  box-shadow: 1px 1px 10px grey;
}
.addcart {
  background-color: skyblue;
  border-radius: 20px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  width: 140px;
  height: 30px;
  margin-left: 40px;
  margin-top: 10px;
}
/* bestSell and reccommend*/

/* book description */
.description {
  margin-top: 130px;
  margin-left: 130px;
  margin-right: 130px;
  display: flex;
  flex-direction: row;
}

.twopic img:nth-child(1) {
  width: 150px;
  height: 200px;
  filter: opacity(30%);
  margin-right: 3%;
}
.twopic img:nth-child(2) {
  width: 150px;
  height: 200px;
  margin-right: 10px;
}
.show img:nth-child(1) {
  width: 400px;
  height: 600px;
  margin-bottom: 10px;
}

.left .right {
  display: flex;
}
.right {
  padding: 0rem 5rem;
}
.text p {
  font-size: 20px;
}
.text strong {
  font-size: 40px;
}
.text b {
  text-decoration: line-through;
  font-size: 25px;
  color: gray;
}
.button {
  display: flex;
  flex-direction: row;
}
.input {
  width: 70px;
  height: 40px;
  background-color: silver;
  text-align: center;
}
.add {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 40px;
  width: 200px;
  height: 40px;
  background-color: rgb(56, 125, 228);
  border-radius: 20px;
  text-align: center;
  color: white;
}
.whitelist {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 20px;
  width: 200px;
  height: 40px;
  background-color: rgb(228, 125, 56);
  border-radius: 20px;
  text-align: center;
  color: white;
}
.share {
  margin-top: 40px;
}
.about {
  margin-left: 130px;
  margin-right: 130px;
  display: flex;
  flex-direction: row;
}
.about p {
  margin-right: 30px;
}
.about p:nth-child(1) {
  text-decoration: underline;
}
.desc {
  margin-left: 130px;
  margin-right: 130px;
}
.show {
  display: flex;
}
.small {
  display: flex;
}
.twopic {
  display: flex;
}
/* book description */

/* cart */

.cart {
  margin-top: 120px;
  margin-left: 150px;
  margin-right: 150px;
}
.cart h3 {
  font-weight: bold;
}
.basket {
  margin-top: 10px;
  width: 800px;
  height: 350px;
  background-color: white;
}
table {
  width: 100%;
  padding: 1rem 1rem;
  align-items: flex-start;
}
th:nth-child(1) {
  width: 340px;
}
th:nth-child(6) {
  text-align: center;
}
table img {
  width: 90px;
  height: 130px;
}
.product {
  display: flex;
  flex-direction: row;
}
.pic {
  display: flex;
  margin-left: 40px;
  margin-top: 20px;
}
.text_physic {
  display: flex;
  margin-left: 20px;
  margin-top: 60px;
  font-weight: lighter;
}
.btn-under {
  display: flex;
  flex-direction: row;
}
.btn-under a {
  display: flex;
  width: 25%;
  height: 35px;
  border-radius: 20px;
  text-decoration: none;
  color: black;
  border: 2px solid black;
  justify-content: center;
  align-items: center;
  margin-right: 10px;
}
.clear {
  display: flex;
  width: 25%;
  height: 35px;
  color: white;
  background-color: black;
  align-items: center;
  justify-content: center;
  border-radius: 20px;
}
.total1 {
  position: fixed;
  width: 340px;
  height: 340px;
  margin-top: -400px;
  left: 70%;
  background-color: rgb(212, 211, 211);
}
.content {
  position: absolute;
  width: 100%;
  padding: 2rem 2rem;
}
.content a {
  text-decoration: none;
}
.allprice,
.priceshipping,
.totalbalance {
  display: flex;
}
.pricebath,
.shippingbath,
.pricebalance {
  display: flex;
}
.price-total,
.shipping,
.balance {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 1rem;
}
.gotopurchase {
  width: 80%;
  height: 40px;
  border-radius: 20px;
  margin-left: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(31, 146, 192);
  color: white;
  font-weight: bold;
}
.total a {
  text-decoration: none;
  color: white;
}

/* cart */

/* purchase */
/* .purchase {
} */
.pur-left {
  width: 57%;
  margin-top: 10%;
  margin-left: 8%;
}
.pur-left h2 {
  font-weight: bold;
}
.form-control {
  border-radius: 5rem;
}
.pur-right {
  /* width: 30%;
  height: 200px;
  margin-top: 10%;
  margin-left: 8%; */
  background-color: yellow;
}
.p-bold {
  font-weight: bold;
}
.border-radio {
  width: 100%;
  height: 100px;
  border: 1.5px solid silver;
  align-items: center;
  display: flex;
  border-radius: 10px;
  margin-bottom: 20px;
}
.form-check label {
  display: flex;
  flex-direction: row;
}
.form-check-input {
  margin-left: 20px !important;
}
.form-check label img {
  width: 10%;
  margin-top: 0px;
  margin-left: 40px;
}
.form-check label p {
  font-weight: bold;
}
.bath-right {
  margin-left: 500px;
}

.shipping-left {
  margin-left: 40px;
}
.total2 {
  position: relative;
  font-weight: bold;
  width: 340px;
  height: 340px;
  margin-top: -1180px;
  left: 70%;
  background-color: rgb(212, 211, 211);
}
